@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

html, body, #app {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-size: 0;
  font-family: 'Montserrat', sans-serif; }

#app {
  opacity: 0;
  height: auto;
  background: #FFB6C1; }

#app img {
  display:block {} }

#detail {
  position: absolute;
  width: 100%;
  height: 100%;
  background:#111 {}
  top: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly; }

#detailImg {
  width: 85%;
  height: 85%; }

#detailTxt {
  color:#ccc {}
  font-size: 20px;
  letter-spacing: 1px; }

svg {
  pointer-events: none;
  position: absolute;
  top: 0;
  left: 0; }

#headlines {
  max-width: 800px;
  min-width: 450px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%); }
